html {
  font-size: 12px;
}

@media only screen and (min-width: 600px) {
  html {
      font-size: 14px;
  }
}

@media only screen and (min-width: 900px) {
  html {
      font-size: 16px;
  }
}

body {
  padding: 0;
  margin: 0;

  --nav-height: 60px;
  --logo-width: 25vw;
  --logo-height: var(--nav-height);

  --color-light: #eee;
  --color-grey: #555;
  --color-blue: #9dc3e6;
  --color-darkblue: #227fc2;
  --color-white: #fff;

  font-family: 'Cinzel', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  position: fixed;
  top: 0;
  width: 100vw;

  display: flex;
  align-items: center;

  font-family: 'Cinzel', serif;
  z-index: 4;

  transition: 0.2ms ease-in;
}

.nav-detached {
  top: 15px;
  --logo-height: calc(1.5 * var(--nav-height));
}

.navbar {
  background: var(--color-blue);
  height: var(--nav-height);
  width: calc(100vw - var(--logo-width));
}

.m-navbar {
  display: none;
  background: var(--color-white);
  height: var(--nav-height);
  width: 200px;
}

.navbar-list {
  background: var(--color-blue);
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: 10vw;
  margin-right: 10px;
  color: #444;

  font-size: 1rem;
  font-weight: 400;
}

.logo {
  height: var(--logo-height);
  width: var(--logo-width);
  background: var(--color-white);
}

.logo-img {
  height: 100%;
}

.masthead {
  background: bisque;
  height: 100vh;
  width: 100vw;

  position: relative;

  /* z-index: -1; */
}

.masthead-carousel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.masthead-tagline {
  /* position: absolute; */
  /* bottom: 0; */
  /* left: 0; */

  width: 100%;

  color: var(--color-white);
  background: var(--color-grey);

  padding: 15px 0;
  margin: 0 0 3px 0;
  text-align: center;
}

.value {
  display: flex;
  width: 100%;
  justify-content: stretch;
}

.value-item {
  color: var(--color-white);
  background: var(--color-grey);
  height: 150px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 10px 0;
  text-align: center;
}

.value-small {
  width: var(--logo-width);
}

.color-blue {
  color: var(--color-darkblue);
  padding: 10px;
}

.value-stretch {
  flex-grow: 1;
  background: #222;
}

.team {
  padding: 60px 0; 
  background-color: var(--color-light);
}

.team-pics {
  width: 80vw;
  margin: 0 auto;

  display: flex;
  justify-content: space-around;
  align-items: center;
}

.team-member {
  text-align: center;
  color: var(--color-darkblue);
  font-size: 1.4rem;
}

.member-name {
  padding: 10px 0;
}

.member-img {
  width: 250px;
  height: 250px;
  border-radius: 100%;

  background: #222;
}

.team-tagline {
  text-align: center;
  margin: 30px auto 5px;

  font-weight: 400;
  font-size: 3rem;

  color: var(--color-grey);
}

.team-learn {
  display: block;
  text-align: center;
  text-decoration: none;
  color: var(--color-darkblue);
  font-size: 1.5rem;
}

@media  only screen and (max-width: 760px) {
  .value {
      height: auto;
      flex-direction: column;
  }
  .value-item {
      width: 100%;
  }

  .team-pics {
      width: 90vw;
  
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
  }

  .team-member {
      padding: 12px;
  }
}