.carousel {
    height: 100%;
    width: 100%;
    position: relative;
}

.carousel-img-cont {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    overflow: hidden;

    z-index: 1;

    transition: 0.3ms ;
}

.carousel-img {
    height: 100%;
    min-width: 100%;
}

.carousel-indicators {
    --indicator-width: 14px;

    position: absolute;
    bottom: 50px;
    left: 0;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 1;
}

.carousel-indicator {
    cursor: pointer;
    width: var(--indicator-width);
    height: var(--indicator-width);
    background: white;
    margin: 0 13px;

    border-radius: 100%;
    
    transition: 0.2s ease-in;
}

.carousel-indicator:hover {
    background: rgb(146, 208, 80);
}

.carousel-arrow {
    --arrow-width: 17px;
    width: 0; 
    height: 0; 
    border-left: var(--arrow-width) solid transparent;
    border-right: var(--arrow-width) solid transparent;
    
    border-top: var(--arrow-width) solid white;

    position: absolute;
    bottom: 20px;
    left: calc(50vw - var(--arrow-width));
    z-index: 1;
}

.carousel-box {
    position: absolute;
    top: calc(2 * var(--logo-height));
    width: var(--logo-width);
    min-height: 50vh;
    padding: 15px 0;
  
    background: rgba(0, 54, 73, 0.7);
    color: var(--color-white);
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 2;
  }
  
  .carousel-title, .carousel-subtitle {
    padding: 15px;
  }
  
  .carousel-title {
    font-weight: 400;
    font-size: 1.6rem;
  }